import React, { useState } from 'react';
import MovieCard from './MovieCard';

function Home() {
  const [string, setString] = useState("");
  const [currentMovie, setCurrentMovie] = useState([]);
  const [welcome, setWelcome] = useState(true);
  const [isLoading, setIsLoading] = useState(false);

  const handleInput= (e)=>{
    e.preventDefault()
    setString(e.target.value);
    setIsLoading(true);
    fetch(`https://api.themoviedb.org/3/search/movie?api_key=a3eca5539e3715256e80a664443dbf05&language=en-US&query=${string}&page=1&include_adult=true`)
    
    .then( data => data.json())
    .then(data => {
      if(data.error){
        setCurrentMovie([]);
      }else{
        setCurrentMovie(data.results)
        setWelcome(false)
        setIsLoading(false);
      }
      console.log(data);
    });
  }

  return (
    <div className="home">
      <input 
        type="text" 
        placeholder="search movie"
        onChange={handleInput}
        value={string}
      />
      {
        currentMovie === undefined ?"":currentMovie.length > 0 && (
          <div >
            {currentMovie.map(movie =>(
              <MovieCard key={movie.id} movie={movie}/>
            ))}
          </div>
        )
      } 

      {
        isLoading?(
          <p>loading...</p>
        ):
        welcome ?(
          <div className="alert alert-info">
          <p>welcome to movie watchlist application</p>
           <p>you can simply add movie to watchlist for future purpose.</p>
           <p>please search for a movie</p>
       </div> 
        ):""
      }
     
    
    </div>
  )
}

export default Home
