import React,{ useContext } from 'react';
import Watchedcard from "./Watchedcard";
import { Moviecontext } from "./Context/Context";

function Watched() {
  const { watched } = useContext(Moviecontext);

  return (
    <div className="watched">
      <h2>Watched</h2>
      {watched.legnth > 0 || watched === undefined?(
        <div className="alert alert-warning">
          <p>watched list is empty</p>
        </div>
      ):""
      }
      {!watched.legnth > 0 || watched === undefined?
      watched.map(movie=>(
        < Watchedcard key={movie.id} movie={movie} /> 
      )):""
      }
    </div>
  )
}

export default Watched


